  width: 100%;
  border-radius: 0.25rem;
  border-width: 1px;
  border-color: var(--color-background-tertiary);
}

.sp-reply-options-content {
  padding: 0.75rem;
}

.sp-reply-option {
  margin-bottom: 0.5rem;
}

.sp-reply-option:last-child {
  margin-bottom: 0px;
}

@media (min-width: 1024px) {
  .sp-reply-options {
    width: 18rem;
  }
}

/* Sidebar - ticket details */

ul.sp-ticket-details {
  list-style-type: none;
}

ul.sp-ticket-details > li {
  margin-bottom: 0.375rem;
}

ul.sp-ticket-details > li:last-child {
  margin-bottom: 0px;
}

ul.sp-ticket-details li .sp-title {
  font-size: 0.875rem;
  color: var(--color-text-secondary);
}

ul.sp-ticket-details li .sp-detail {
  font-weight: 700;
}

ul.sp-ticket-details li .sp-detail a.sp-detail-link {
  border-bottom-width: 1px;
  border-style: dotted;
  border-color: var(--color-primary-800);
}

ul.sp-ticket-details li .sp-detail a.sp-detail-link:hover {
  border-style: solid;
}

ul.sp-ticket-details li .sp-detail > select,
ul.sp-ticket-details li .sp-detail > .selectize-control {
  margin-top: 0.25rem;
  width: 100%;
  max-width: 100%;
}

ul.sp-ticket-details li .sp-detail .create-new-user {
  font-weight: 400;
}

ul.sp-ticket-details li .sp-detail .create-new-user a.new-user-toggle {
  border-width: 0px;
}

ul.sp-ticket-details li .sp-detail .new-user-form input,
ul.sp-ticket-details li .sp-detail .new-user-form .selectize-control {
  margin-bottom: 0.5rem;
  width: 100%;
}

ul.sp-ticket-details li .sp-detail .selectize-control {
  font-weight: 400;
}

/* Code Editor */

.sp-editor-preview {
  position: absolute;
  top: 0px;
  z-index: 10001;
  width: 100%;
  overflow: auto;
  border-radius: 0.25rem;
  border-width: 1px;
  border-color: var(--color-background-tertiary);
  background-color: var(--color-background-secondary);
  padding: 0.75rem;
}

.sp-editor-preview.loadinggif:before {
  position: absolute;
  top: 50%;
  inset-inline-start: 50%;
  z-index: 10001;
  margin-top: -3rem;
  margin-inline-start: -2rem;
  font-family: var(--font-icon);
  font-size: 4rem;
  font-weight: 700;
  color: var(--color-primary-600);
}

.sp-editor-preview.loadinggif:is(.sp-theme-dark *):before {
  color: var(--color-primary-200);
}

.sp-editor-preview.loadinggif:before {
  animation: fa-spin 2s infinite linear;
  content: "\f013";
}

/* Comments */

.sp-comment {
  padding: 1rem;
}

.sp-comment .sp-comment {
  border-width: 0px;
  padding: 0px;
}

.sp-comment .sp-comment:first-child {
  margin-top: 0.75rem;
}
